.header-top {
  position: fixed;
  z-index: 1000;
  width: 100vw;
  padding: 0.1rem;
  border-bottom: 1px solid #181b1d;
  background: #23272a;
  font-family: Helvetica;
  color: white;
}

.header-top div {
  font-family: Arial;
  font-weight: bold;
  font-size: 1.1rem;
  margin: 0.7rem;
}

@media (max-width: 650px) {
  .header-top div{
    font-size: 0.5rem;
  }
}

.header-top a {
  color: white;
}